*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.popular {
  width: 310px;
}

.popular .popular-box {
  flex-direction: column;
  gap: 24px;
  margin-bottom: 40px;
  display: flex;
}

.popular-title {
  color: var(--txt-color);
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
}

.photo-card_recipes {
  width: 332px;
  width: 176px;
  background-color: #f8f8f8;
  gap: 8px;
  display: flex;
}

.popular-foto {
  width: 64px;
  height: 64px;
  border-radius: 8px;
}

.info_recipes {
  width: 240px;
}

.recipe-title {
  color: var(--txt-color);
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4;
}

.box {
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.info-item_recipes {
  color: var(--txt-color);
  text-align: left;
  width: 252px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
}

@media screen and (min-width: 375px) {
  .popular {
    width: 332px;
  }

  .info_recipes {
    width: 252px;
  }
}

@media screen and (min-width: 768px) {
  .popular {
    width: 176px;
    gap: 32px;
  }

  .popular .popular-box {
    gap: 16px;
  }

  .photo-card_recipes {
    width: 176px;
    gap: 8px;
    display: flex;
  }

  .popular-foto {
    width: 48px;
    height: 48px;
  }

  .info_recipes {
    width: 120px;
  }

  .recipe-title {
    width: 91px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  .box {
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
  }

  .info-item_recipes {
    width: 120px;
    font-size: 10px;
  }
}

@media screen and (min-width: 1280px) {
  .popular {
    width: 235px;
    gap: 32px;
  }

  .popular .popular-box {
    gap: 32px;
  }

  .popular-title {
    font-size: 24px;
    line-height: 28px;
  }

  .photo-card_recipes {
    width: 332px;
    gap: 16px;
    display: flex;
  }

  .popular-foto {
    width: 64px;
    height: 64px;
  }

  .info_recipes, .info-item_recipes {
    width: 155px;
  }
}

[data-theme="dark"] .photo-card_recipes {
  background-color: #161616;
}

