.container {
  width: 100%;
  margin: 0 auto;
}

.header {
  padding-top: 20px;
}

.nav-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-header {
  text-align: center;
  letter-spacing: -.64px;
  color: var(--accent);
  font-size: 16px;
  font-weight: 600;
  line-height: 1.12;
}

.logo-color {
  color: var(--txt-color);
}

.shopping-link {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.shopping-icon {
  fill: var(--txt-color);
}

.shopping-icon:hover, .shopping-icon:focus {
  fill: var(--accent);
}

.burger-menu {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.burger-menu-icon {
  width: 32px;
  height: 32px;
  fill: var(--txt-color);
}

.burger-menu-icon:hover, .burger-menu-icon:focus {
  fill: var(--accent);
}

.header-btn-box {
  align-items: center;
  gap: 14px;
  display: flex;
}

.input-switcher[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
  display: none;
}

.input-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 46px;
  height: 20px;
  background: #cecdcd;
  border-radius: 18px;
  display: block;
  position: relative;
}

.input-label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: var(--white_2);
  border-radius: 18px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.input-switcher:checked + .input-label {
  background: #9bb537;
  transition: all .3s;
}

.input-switcher:checked + .input-label:after {
  transition: all .3s;
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.input-label:active:after {
  width: 26px;
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: all .3s;
  display: flex;
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: rgba(0, 0, 0, .1);
}

.menu-container {
  width: 40%;
  min-width: 220px;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: var(--bg-mob-menu);
  z-index: 2000;
  padding-top: 200px;
  transition: opacity .25s ease-in-out, visibility .25s ease-in-out, transform .25s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(-100%);
}

.menu-container.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}

.block {
  overflow: hidden;
}

.menu-container .menu-toggle {
  color: var(--white_2);
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .nav-link {
  color: var(--white_2);
  text-align: center;
  padding: 0 50px 16px;
  text-decoration: none;
  display: block;
}

.nav-link {
  color: var(--txt-color);
  color: inherit;
  font-size: 12px;
  line-height: 1.25;
  text-decoration: none;
  position: relative;
}

.nav-link:after {
  content: "";
  height: 2px;
  width: 0;
  background-color: currentColor;
  transition: width .3s, left .3s;
  position: absolute;
  bottom: 20px;
  left: 50%;
}

.nav-link:hover:after, .nav-link:focus:after {
  width: 100%;
  left: 0;
}

.mobile-menu .current-mob {
  color: var(--current-mob);
}

.theme-switcher.burger {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

@media screen and (min-width: 375px) {
  .nav-link {
    color: inherit;
    text-decoration: none;
    position: relative;
  }

  .nav-link:after {
    content: "";
    height: 2px;
    width: 0;
    background-color: currentColor;
    transition: width .3s, left .3s;
    position: absolute;
